<template>
	<div>
		
		
		<!-- wrap -->
		<div id="wrap">
			<!-- header -->
			<header id="sub">
			<div class="header_wrap_mortuary">
				<h1>빈소/입관실 등록</h1>
				<span>※ 빈소,입관실 수만큼 추가하신 후 입력해주세요</span>
			</div>
			</header>
			<!-- header -->
			<!-- content -->
			<div id="mortuary_reg">
				<dl class="form-style-list">
					<dt class="form-style-list-key">
						<span class="tit" style="padding-bottom: 0;">빈소</span>
					</dt>
					<dd class="form-style-list-val">
						<span class="ui-input-block" v-for="(mortuary,index) in getMortuaryList" :key="index">
							<input type="text" id="ribbonLeft" name="ribbonLeft" class="ui-input" maxlength="30" placeholder="빈소명을 입력해주세요" v-model="mortuary.mortuaryNumber">
							<button type="button" onclick=""><span class="ico-plus"><a @click="addMortuary"><img src="@/assets/images/sub/ico_plus.png" alt="추가"></a></span></button>
							<button type="button" onclick=""><span class="ico-minus"><a @click="removeMortuary(index)"><img src="@/assets/images/sub/ico_minus.png" alt="빼기"></a></span></button>
						</span>
						<!--+버튼클릭시 아래 빈소명입력박스가 클릭할때마다 생성됨s-->
					</dd>
				</dl>
				<div class="register_submit">
					<button type="button" class="btn_order" @click="registMortuary">등록하기</button>
				</div>
				<dl class="form-style-list">
					<dt class="form-style-list-key">
						<span class="tit">입관실</span>
					</dt>
					<span class="ui-input-block" v-for="(enterRoom,index) in getEnterRoomList" :key="index">
						<input type="text" id="ribbonLeft" name="ribbonLeft" class="ui-input" maxlength="30" placeholder="입관실명을 입력해주세요" v-model="enterRoom.enterRoomNumber">
						<button type="button" onclick=""><span class="ico-plus"><a  @click="addEnterRoom"><img src="@/assets/images/sub/ico_plus.png" alt="추가"></a></span></button>
						<button type="button" onclick=""><span class="ico-minus"><a  @click="removeEnterRoom(index)"><img src="@/assets/images/sub/ico_minus.png" alt="빼기"></a></span></button>
					</span>
				</dl>
				<div class="register_submit">
					<button type="button" class="btn_order" @click="registEnterRoom">등록하기</button>
				</div>	
			</div>
			<!-- //content -->
			<!-- footer -->
			<Footer/>
			<!-- //footer -->
		</div>
	</div>
</template>
<script>
import  Footer from '@/components/common/Footer'
import { mapGetters } from 'vuex'
import myMixin from '@/mixin/mixin'
export default {
  name: 'ObituaryReg',
	mixins: [myMixin],
  components: { 
      Footer
	},
  	props: {
  	},
    data(){
        return{
		}
	},
	computed:{
		...mapGetters(['getMortuaryList','getEnterRoomList']),
	},
   	mounted() {
           this.requestGetMortuaryAndEnterRoom()
	},
	beforeDestroy(){
		this.$store.commit('NOTICE_INIT')
	},
	methods:{
		registEnterRoom(){
			let enterRoomList = []
			for(let i=0;i<this.getEnterRoomList.length;i++){
				enterRoomList.push({
					id:this.getEnterRoomList[i].id,
					enterRoomNumber:this.getEnterRoomList[i].enterRoomNumber

				})
			}
			const parameter = {
				enterRoom: enterRoomList
			}
           this.$store.dispatch('ENTER_ROOM_REGIST',parameter)


		},
		registMortuary(){

			let mortuaryList = []
			for(let i=0;i<this.getMortuaryList.length;i++){
				mortuaryList.push({
					id:this.getMortuaryList[i].id,
					mortuaryNumber:this.getMortuaryList[i].mortuaryNumber

				})	
			}
			const parameter = {
				mortuary:mortuaryList,
			}
           this.$store.dispatch('MORTUARY_REGIST',parameter)

		},
		addMortuary(){
           this.$store.commit('MORTUARY_ADD_LIST')

		},
		removeMortuary(index){
			const parameter = {
				index:index
			}
           this.$store.commit('MORTUARY_REMOVE_LIST',parameter)
		},
		addEnterRoom(){
           this.$store.commit('ENTER_ROOM_ADD_LIST')

		},
		removeEnterRoom(index){
			const parameter = {
				index:index
			}
           this.$store.commit('ENTER_ROOM_REMOVE_LIST',parameter)
		},
		requestGetMortuaryAndEnterRoom(){
		    this.$store.dispatch('REQUEST_MORTUARY_AND_ENTER_LIST')
		}
	}
}
</script>

